import React from "react";

export const List = ({ peopleProp }) => {
  return (
    <div>
      {peopleProp.map((item) => {
        const { Id, Name, Image, Age } = item;
        return (
          <div key={Id} className="person">
            <img src={Image} alt="" />
            <div className="info">
              <h4>{Name}</h4>
              <p>{Age}</p>
            </div>
          </div>
        );
      })}
    </div>
  );
};
